@import url("https://fonts.googleapis.com/css2?family=Chivo+Mono,wght@1,300&display=swap");

body {
  margin: 0;
  padding: 0;
  width: 100vw !important;
  height: 100vh !important;
  /* overflow: hidden; */
}

* {
  font-family: "Chivo Mono", monospace;
}

#root {
    width: 100vw;
    min-height: 100vh;
}

.cursor {
  color: #b2d1c8;
}

canvas,
.renderer {
  width: 100%;
  height: 100%;
  position: fixed;
  top: 0;
  left: 0;
  z-index: -1;
}

*::-webkit-scrollbar {
  width: 0px;
}

